webpackJsonp([133001515947093],{

/***/ 427:
/***/ (function(module, exports) {

	module.exports = {"data":{"site":{"siteMetadata":{"title":"Takumon Blog","author":"Takuto Inoue"}},"markdownRemark":{"id":"/Users/inouetakumon/git/blog/src/pages/2018/09/16/index.md absPath of file >>> MarkdownRemark","html":"<h2>なにこれ</h2>\n<p>Gatsby製ブログでSNS対応を実施した時のメモです。<br>\n<strong>(1) SNSシェアボタン設置</strong>して <strong>(2) 自分のSNSへのリンク設置</strong>することでブログフッターの見栄えがちょっと良い感じになりました。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/blog-footer-b144206bd9e6f72bee5020c93a7da93b-0136e.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 59.01162790697675%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"ブログフッタ\"\n        title=\"\"\n        src=\"/static/blog-footer-b144206bd9e6f72bee5020c93a7da93b-fb8a0.png\"\n        srcset=\"/static/blog-footer-b144206bd9e6f72bee5020c93a7da93b-1a291.png 148w,\n/static/blog-footer-b144206bd9e6f72bee5020c93a7da93b-2bc4a.png 295w,\n/static/blog-footer-b144206bd9e6f72bee5020c93a7da93b-fb8a0.png 590w,\n/static/blog-footer-b144206bd9e6f72bee5020c93a7da93b-0136e.png 688w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>またSNSシェア時にわかりやすくするために<strong>(3) OGPタグなるものを設定</strong>しました。Twitterで呟くと画像、タイトル、記事概要を表示できるようになります。<br>はてなブログとかQiitaみたいな感じですね。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/tweet-sample-with-number-32788568e53b55ca649719666998c2b0-27ff0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 38.11129848229342%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"つぶやきサンプル\"\n        title=\"\"\n        src=\"/static/tweet-sample-with-number-32788568e53b55ca649719666998c2b0-fb8a0.png\"\n        srcset=\"/static/tweet-sample-with-number-32788568e53b55ca649719666998c2b0-1a291.png 148w,\n/static/tweet-sample-with-number-32788568e53b55ca649719666998c2b0-2bc4a.png 295w,\n/static/tweet-sample-with-number-32788568e53b55ca649719666998c2b0-fb8a0.png 590w,\n/static/tweet-sample-with-number-32788568e53b55ca649719666998c2b0-27ff0.png 593w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>1. SNSシェアボタンを設置</h2>\n<p><a href=\"https://github.com/nygardk/react-share\">react-share</a>で超簡単に設置できます。</p>\n<p>ここではFacebook、Google、Linkedin、Twitterのアイコンを設置します。<br>\n基本は記事のURLを<code class=\"language-text\">url</code>属性に、アイコンサイズを<code class=\"language-text\">icon</code>属性に指定します。<br>\nTwitterの場合はブロガーのTwitterアカウントを<code class=\"language-text\">via</code>属性に、ブログ記事タイトルを<code class=\"language-text\">title</code>属性に指定します。</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FacebookShareButton</span> <span class=\"token attr-name\">url</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>link<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FacebookIcon</span> <span class=\"token attr-name\">size</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">32</span><span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">round</span> <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>FacebookShareButton</span><span class=\"token punctuation\">></span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>GooglePlusShareButton</span> <span class=\"token attr-name\">url</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>link<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>GooglePlusIcon</span> <span class=\"token attr-name\">size</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">32</span><span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">round</span> <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>GooglePlusShareButton</span><span class=\"token punctuation\">></span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>LinkedinShareButton</span> <span class=\"token attr-name\">url</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>link<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>LinkedinIcon</span> <span class=\"token attr-name\">title</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>title<span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">size</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">32</span><span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">round</span> <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>LinkedinShareButton</span><span class=\"token punctuation\">></span></span>\n\n<span class=\"gatsby-highlight-code-line\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>TwitterShareButton</span> <span class=\"token attr-name\">title</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>title<span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">via</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>twitterUserName<span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">url</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>link<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">></span></span>\n</span>  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>TwittearIcon</span> <span class=\"token attr-name\">size</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">32</span><span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">round</span> <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>TwitterShareButton</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<p>そうすると下記のように画面にSNSシェアアイコンが表示されます。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/sns-share-icons-d0d7eb87f24ffe765de329c18b8cf04a-1d2d3.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 205px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 33.65853658536586%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"SNSシェアアイコン\"\n        title=\"\"\n        src=\"/static/sns-share-icons-d0d7eb87f24ffe765de329c18b8cf04a-1d2d3.png\"\n        srcset=\"/static/sns-share-icons-d0d7eb87f24ffe765de329c18b8cf04a-1eb4e.png 148w,\n/static/sns-share-icons-d0d7eb87f24ffe765de329c18b8cf04a-1d2d3.png 205w\"\n        sizes=\"(max-width: 205px) 100vw, 205px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>はてブ追加ボタンは下記のようにします。</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Helmet</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>script</span> <span class=\"token attr-name\">type</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>text/javascript<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>//b.st-hatena.com/js/bookmark_button.js<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">charset</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>utf-8<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">async</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>async<span class=\"token punctuation\">\"</span></span> <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Helmet</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span>\n  <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>http://b.hatena.ne.jp/entry/<span class=\"token punctuation\">\"</span></span>\n  <span class=\"token attr-name\">className</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>hatena-bookmark-button<span class=\"token punctuation\">\"</span></span>\n  <span class=\"token attr-name\">data-hatena-bookmark-layout</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>vertical-normal<span class=\"token punctuation\">\"</span></span>\n  <span class=\"token attr-name\">data-hatena-bookmark-lang</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>ja<span class=\"token punctuation\">\"</span></span>\n  <span class=\"token attr-name\">title</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>このエントリーをはてなブックマークに追加<span class=\"token punctuation\">\"</span></span>\n  <span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>img</span>\n    <span class=\"token attr-name\">src</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>//b.st-hatena.com/images/entry-button/button-only@2x.png<span class=\"token punctuation\">\"</span></span>\n    <span class=\"token attr-name\">alt</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>このエントリーをはてなブックマークに追加<span class=\"token punctuation\">\"</span></span>\n    <span class=\"token attr-name\">width</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>20<span class=\"token punctuation\">\"</span></span>\n    <span class=\"token attr-name\">height</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>20<span class=\"token punctuation\">\"</span></span>\n    <span class=\"token attr-name\">style</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>border<span class=\"token punctuation\">:</span> <span class=\"token string\">'none'</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span>\n  <span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<p>そうすると下記のように画面にはてブ追加アイコンが表示されます。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hatebu1-071417b7b9f032c217826eb11a0a2698-93e48.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 114px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 60.526315789473685%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"はてブシェアアイコン\"\n        title=\"\"\n        src=\"/static/hatebu1-071417b7b9f032c217826eb11a0a2698-93e48.png\"\n        srcset=\"/static/hatebu1-071417b7b9f032c217826eb11a0a2698-93e48.png 114w\"\n        sizes=\"(max-width: 114px) 100vw, 114px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>クリックするとウィジェットが表示されます。(<code class=\"language-text\">bookmark_button.js</code>がやってくれています。)\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hatebu2-10e6ad7b12fcc2c5a07352252251d509-5119a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 384px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 132.29166666666669%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"はてブシェアアイコンウィジェット\"\n        title=\"\"\n        src=\"/static/hatebu2-10e6ad7b12fcc2c5a07352252251d509-5119a.png\"\n        srcset=\"/static/hatebu2-10e6ad7b12fcc2c5a07352252251d509-0a242.png 148w,\n/static/hatebu2-10e6ad7b12fcc2c5a07352252251d509-f057f.png 295w,\n/static/hatebu2-10e6ad7b12fcc2c5a07352252251d509-5119a.png 384w\"\n        sizes=\"(max-width: 384px) 100vw, 384px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>2. 自分のSNSへのリンクを設置</h2>\n<p>SNSへのリンクアイコンは<a href=\"https://github.com/FortAwesome/react-fontawesome\">react-fontawesome</a>を使います。react-fontawesomeをインストールします。</p>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">npm install --save @fortawesome/react-fontawesome\nnpm install --save @fortawesome/fontawesome-svg-core\nnpm install --save @fortawesome/free-brands-svg-icons\nnpm install --save @fortawesome/free-solid-svg-icons\nnpm install --save @fortawesome/free-regular-svg-icons</code></pre>\n      </div>\n<p>コンポーネントで下記のようにします。\nスタイルの細かい部分は省略していますがざっくりこんな感じです</p>\n<ul>\n<li>GitHub → Font Awsomeアイコン使って背景をダークグレイにする</li>\n<li>Twitter → Font Awsomeアイコンを使って背景を水色にする</li>\n<li>Qiita → Font Awsomeに用意がないので、代わりに虫眼鏡を使って背景を黄緑にしてQiitaっぽくする</li>\n</ul>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\">・・・\n\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> FontAwesomeIcon <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'@fortawesome/react-fontawesome'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> faGithubSquare<span class=\"token punctuation\">,</span> faTwitterSquare <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'@fortawesome/free-brands-svg-icons'</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> faSearch <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'@fortawesome/free-solid-svg-icons'</span>\n\n・・・\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">Bio</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      ・・・\n<span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span>\n</span><span class=\"gatsby-highlight-code-line\">        <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://github.com/Takumon<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n</span><span class=\"gatsby-highlight-code-line\">       <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FontAwesomeIcon</span>\n</span><span class=\"gatsby-highlight-code-line\">         <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>#333<span class=\"token punctuation\">\"</span></span>\n</span><span class=\"gatsby-highlight-code-line\">         <span class=\"token attr-name\">icon</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>faGithubSquare<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span>\n</span><span class=\"gatsby-highlight-code-line\">        <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://twitter.com/inouetakumon<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n</span><span class=\"gatsby-highlight-code-line\">        <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FontAwesomeIcon</span>\n</span><span class=\"gatsby-highlight-code-line\">          <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>#3eaded<span class=\"token punctuation\">\"</span></span>\n</span><span class=\"gatsby-highlight-code-line\">          <span class=\"token attr-name\">icon</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>faTwitterSquare<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>a</span><span class=\"token punctuation\">></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>a</span>\n</span><span class=\"gatsby-highlight-code-line\">        <span class=\"token attr-name\">href</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>https://qiita.com/Takumon<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n</span><span class=\"gatsby-highlight-code-line\">        <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FontAwesomeIcon</span>\n</span><span class=\"gatsby-highlight-code-line\">          <span class=\"token attr-name\">color</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>white<span class=\"token punctuation\">\"</span></span>\n</span><span class=\"gatsby-highlight-code-line\">          <span class=\"token attr-name\">icon</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>faSearch<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span>      ・・・\n    <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>ここまでくると、下記のようなアイコンが画面に追加されています。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/my-sns-icons-dcdd6bd9dc56f2fdd0408117969af22c-3135f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 333px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 26.426426426426424%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"自分のSNSリンク\"\n        title=\"\"\n        src=\"/static/my-sns-icons-dcdd6bd9dc56f2fdd0408117969af22c-3135f.png\"\n        srcset=\"/static/my-sns-icons-dcdd6bd9dc56f2fdd0408117969af22c-2c691.png 148w,\n/static/my-sns-icons-dcdd6bd9dc56f2fdd0408117969af22c-4b7bd.png 295w,\n/static/my-sns-icons-dcdd6bd9dc56f2fdd0408117969af22c-3135f.png 333w\"\n        sizes=\"(max-width: 333px) 100vw, 333px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h2>3. OGPタグを設定</h2>\n<p>OGPとは、「Open Graph Protcol」の略でFacebookやTwitterなどのSNSでシェアした際に、\nWEBページのタイトルやイメージ画像、説明文などをわかりやすく伝えるためのHTML要素です。\nOGPをWebページで設定することでユーザーに対してWEBページの内容を詳しく伝えることができます。</p>\n<h3>OGPタグ定義</h3>\n<p>このブログはReactを使って書かれているので、ReactHelmetを使ってOPG専用のコンポーネントを作りました。\nブログのトップと記事詳細で設定する値が違うので、そこは引数でとれるようにしてあります。</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token keyword\">import</span> React <span class=\"token keyword\">from</span> <span class=\"token string\">'react'</span>\n<span class=\"token keyword\">import</span> Helmet <span class=\"token keyword\">from</span> <span class=\"token string\">'react-helmet'</span>\n\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span>\n  blogTitle<span class=\"token punctuation\">,</span>\n  blogUrl<span class=\"token punctuation\">,</span>\n  blogDescription<span class=\"token punctuation\">,</span>\n  blogImageUrl<span class=\"token punctuation\">,</span>\n  facebookAppId<span class=\"token punctuation\">,</span>\n  blogAuthorTwitterUserName<span class=\"token punctuation\">,</span>\n<span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'../config/blog-config.js'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"gatsby-highlight-code-line\"><span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token keyword\">function</span> <span class=\"token function\">Ogp</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>isRoot<span class=\"token punctuation\">,</span> title<span class=\"token punctuation\">,</span> description<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n</span>  <span class=\"token keyword\">const</span> type <span class=\"token operator\">=</span> isRoot <span class=\"token operator\">?</span> <span class=\"token string\">'website'</span> <span class=\"token punctuation\">:</span> <span class=\"token string\">'article'</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">return</span><span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Helmet</span><span class=\"token punctuation\">></span></span>\n<span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>og:title<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>title <span class=\"token operator\">||</span> blogTitle<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>og:description<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>description <span class=\"token operator\">||</span> blogDescription<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>og:url<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>blogUrl<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>og:type<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>type<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>og:site_name<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>blogTitle<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>og:image<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>blogImageUrl<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">property</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>fb:app_id<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>facebookAppId<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>twitter:card<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>summary<span class=\"token punctuation\">\"</span></span> <span class=\"token punctuation\">/></span></span>\n</span><span class=\"gatsby-highlight-code-line\">      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>twitter:site<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token template-string\"><span class=\"token string\">`@</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>blogAuthorTwitterUserName<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n</span>    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Helmet</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>※<code class=\"language-text\">blog-config.js</code>はブログタイトルなどの設定を外出しした設定ファイル<br>\nいろいろあるプロパティは下記の通り</p>\n<table>\n<thead>\n<tr>\n<th align=\"left\">プロパティ名</th>\n<th align=\"left\">説明</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td align=\"left\">og:title</td>\n<td align=\"left\">ページのタイトル。SNS共有時は、headのtitleタグよりも優先して使われます。</td>\n</tr>\n<tr>\n<td align=\"left\">og:description</td>\n<td align=\"left\">ページの説明。</td>\n</tr>\n<tr>\n<td align=\"left\">og:url</td>\n<td align=\"left\">ページのURL。</td>\n</tr>\n<tr>\n<td align=\"left\">og:type</td>\n<td align=\"left\">ページの属性、だいたい\n<code class=\"language-text\">website</code>\nか\n<code class=\"language-text\">article</code>\nを指定します。詳細は\n<a href=\"http://ogp.me/#types\">ココ</a>\n参照</td>\n</tr>\n<tr>\n<td align=\"left\">og:site_name</td>\n<td align=\"left\">Webサイト名。</td>\n</tr>\n<tr>\n<td align=\"left\">og:image</td>\n<td align=\"left\">ページのイメージ画像</td>\n</tr>\n<tr>\n<td align=\"left\">fb:app_id</td>\n<td align=\"left\">FacebookのアプリID。FacebookでOGPを表示させる際に必須。アプリIDの取り方は\n<a href=\"https://design-plus1.com/tcd-w/2018/01/facebook_app_id.html\">FacebookのOGP設定に必要なfb:app IDの取得方法</a>\n参照。</td>\n</tr>\n<tr>\n<td align=\"left\">twitter:card</td>\n<td align=\"left\">OGPのレイアウトのタイプ。詳細は\n<a href=\"https://saruwakakun.com/html-css/reference/twitter-card\">【2018年版】Twitterカードとは？使い方と設定方法まとめ</a>\n参照。</td>\n</tr>\n<tr>\n<td align=\"left\">twitter:site</td>\n<td align=\"left\">Twitterのユーザ名。</td>\n</tr>\n</tbody>\n</table>\n<h3>OGPタグ呼び出し</h3>\n<p>タイトルやURLはページ個別で設定が必要です。\nGatsby製ブログでは下記2カ所でOGPタグを呼び出します。</p>\n<h4>ブログトップページでの呼び出し</h4>\n<p><strong>src/layouts/index.js</strong><br>\n※全体のテンプレートなのでURLがブログトップページの時のみOGPタグを呼び出します。</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Ogp</span> <span class=\"token attr-name\">isRoot</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>isRoot<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span></code></pre>\n      </div>\n<h4>ブログ記事ページでの呼び出し</h4>\n<p><strong>src/templates/blog-post.js</strong></p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Ogp</span> <span class=\"token attr-name\">isRoot</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>isRoot<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n  isRoot<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token boolean\">false</span><span class=\"token punctuation\">}</span>\n  title<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>post<span class=\"token punctuation\">.</span>frontmatter<span class=\"token punctuation\">.</span>title<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\"> | </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>siteTitle<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">`</span></span><span class=\"token punctuation\">}</span>\n<span class=\"gatsby-highlight-code-line\">  description<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token function\">sumarrize</span><span class=\"token punctuation\">(</span>post<span class=\"token punctuation\">.</span>html<span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">></span>\n</span></code></pre>\n      </div>\n<p>4行目の<code class=\"language-text\">sumarrize</code>メソッドではブログ記事(HTML形式)から<a href=\"https://github.com/ericnorris/striptags\">striptags</a>というライブラリを使ってテキストを抽出し、冒頭120文字を1行にして返すようにしています。</p>\n<div class=\"gatsby-highlight\" data-language=\"jsx\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token keyword\">function</span> <span class=\"token function\">sumarrize</span><span class=\"token punctuation\">(</span>html<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> postContent <span class=\"token operator\">=</span> <span class=\"token function\">striptags</span><span class=\"token punctuation\">(</span>html<span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">replace</span><span class=\"token punctuation\">(</span><span class=\"token regex\">/\\r?\\n/g</span><span class=\"token punctuation\">,</span> <span class=\"token string\">''</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">trim</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">return</span> postContent<span class=\"token punctuation\">.</span>length <span class=\"token operator\">&lt;=</span> <span class=\"token number\">120</span>\n    <span class=\"token operator\">?</span> postContent\n    <span class=\"token punctuation\">:</span> postContent<span class=\"token punctuation\">.</span><span class=\"token function\">slice</span><span class=\"token punctuation\">(</span><span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">120</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">+</span> <span class=\"token string\">'...'</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>※<a href=\"https://github.com/ericnorris/striptags\">striptags</a>は非常に便利なライブラリですが、1点だけ注意点があって、最新バージョンの3系(2018/9/16現在)はGatsbyビルド時にエラーになるため<strong>2系を使用してください</strong>。Gatsbyではビルド時に<a href=\"https://github.com/mishoo/UglifyJS2\">Uglifyjs</a>を使用しているためES6のコードをコンパイルできません。</p>\n<p>これでTwitterやFacebookで呟くと下記のようにいい感じに画像とタイトル、説明文を表示してくれるようになります。\n\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/tweet-sample-b0cd0202e80e38f1c88291e73f0b8290-27ff0.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 38.11129848229342%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"つぶやきサンプル\"\n        title=\"\"\n        src=\"/static/tweet-sample-b0cd0202e80e38f1c88291e73f0b8290-fb8a0.png\"\n        srcset=\"/static/tweet-sample-b0cd0202e80e38f1c88291e73f0b8290-1a291.png 148w,\n/static/tweet-sample-b0cd0202e80e38f1c88291e73f0b8290-2bc4a.png 295w,\n/static/tweet-sample-b0cd0202e80e38f1c88291e73f0b8290-fb8a0.png 590w,\n/static/tweet-sample-b0cd0202e80e38f1c88291e73f0b8290-27ff0.png 593w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>実際にどんな感じになるかは下記サイトで確認できます。</p>\n<ul>\n<li>Twitter → <a href=\"https://cards-dev.twitter.com/validator\">Card validator</a></li>\n<li>Facebook → <a href=\"https://developers.facebook.com/tools/debug/\">シェアデバッガー</a></li>\n</ul>\n<h2>まとめ</h2>\n<p>今回はGatsby製ブログでSNSシェアボタンや自分のSNSへのリンクを設置し、さらにOGPタグを設置して\nTwitterやFacebookなどでのシェアをより効果的にする方法をメモとして残しました。\nGatsbyはReact製ということもあり、かなり簡単に実現できますし、グっとブログっぽくなるので是非みなさんも挑戦してみてください。</p>\n<h2>参考</h2>\n<ul>\n<li>\n<p>OGPタグ系関連</p>\n<ul>\n<li>\n<p>参考にしたサイト</p>\n<ul>\n<li><a href=\"https://ferret-plus.com/610\">Facebook・TwitterのOGP設定方法まとめ｜ferret [フェレット]</a></li>\n<li><a href=\"https://akameco.github.io/blog/react-helmet/\">React Helmetを使ってOGP対応した | akameco Blog</a></li>\n<li><a href=\"https://design-plus1.com/tcd-w/2018/01/facebook_app_id.html\">FacebookのOGP設定に必要なfb:app IDの取得方法</a></li>\n</ul>\n</li>\n<li>\n<p>下記で実際の表示結果のプレビューが確認できます。</p>\n<ul>\n<li>Twitter → <a href=\"https://cards-dev.twitter.com/validator\">Card validator</a></li>\n<li>Facebook → <a href=\"https://developers.facebook.com/tools/debug/\">シェアデバッガー</a></li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/ericnorris/striptags\">striptags</a></p>\n<ul>\n<li>HTML文字列から単純テキストを抽出してくれるライブラリです。</li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/gatsbyjs/gatsby/issues/3780\">GatsbyはES6未対応 | GitHub issues</a></p>\n<ul>\n<li>GatsbyはビルドでJSファイル最初化時にUglifyを使っており、uglifyがES6未対応なのでES6のコードをコンパイルしようとするとエラーになってしまいます。</li>\n</ul>\n</li>\n</ul>\n</li>\n</ul>","frontmatter":{"title":"Gatsby製ブログでSNS対応(SNSシェアボタン、OGPタグ追加)","date":"2018/09/16"}}},"pathContext":{"slug":"/2018/09/16/","previous":{"fields":{"slug":"/2018/09/12/"},"frontmatter":{"title":"GitHub Pagesを独自ドメイン化 + Https化"}},"next":null}}

/***/ })

});


